@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');
* {
  margin: 0;
  font-family: 'Quicksand', sans-serif;
}
.navbar a {
  margin: 16px;
  border: 2px solid black;
  padding:16px;
  border-radius: 10px;
  text-decoration: none;
}
.blog-preview a {
  text-decoration: none;
}
nav {
  display: flex;
  justify-content: space-between;
  margin: auto;
  padding:32px;
  width: 60vw;
  border-bottom: 5px solid rgba(67, 67, 67, 0.111);
}
nav a:hover{
  font-weight: 500;
  background-color: rgb(0, 104, 160);
  color: white;
}
nav h1 {
  color: rgb(0, 104, 160);
  
}
.content{
  max-width: 60vw;
  margin: 48px auto;
  padding: 32px;
}

.blog-preview{
  padding: 10px 16px;
  margin: 32px 0;
  border-bottom: 1px solid #fafafa;
}

.blog-preview:hover{
  box-shadow: 1px 3px 5px rgba(0,0,0,0.1);
}
.blog-preview h2 {
  font-size: 20px;
  color: rgb(0, 104, 160);
  margin-bottom: 8px;
}

.blog-details h2{
  font-size: 20px;
  color: rgb(0, 104, 160);
  margin-bottom: 10px;
}

.blog-details div{
  margin: 20px 0;
}

.blog-details button {
  background:rgb(0, 104, 160);
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}

.create {
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
}
.create label{
  text-align: left;
  display: block;
}
.create h2{
  font-size: 20px;
  color:rgb(0, 104, 160);
  margin-bottom: 30px;
}
.create :is(input, textarea, select){
  width: 100%;
  padding: 8px 16px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
}

.create button {
  background: rgb(0, 104, 160);
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}